.form label {
  font-family: "PT Mono", "Courier New", Courier, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8); }

.form button[type='submit'] {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: #00B2FF; }
  @media (max-width: 767px) {
    .form button[type='submit'] {
      margin-bottom: 29px; } }
  .form button[type='submit']:hover {
    opacity: 0.7; }
  .form button[type='submit']:active {
    opacity: 0.3; }

.form input[type='checkbox']:focus + .custom-checkbox,
.form input[type='checkbox']:hover + .custom-checkbox {
  opacity: 0.7;
  outline-offset: 2px;
  outline: 1px dashed #C4C4C4; }
